}
.player__page__info{
    margin-bottom: 30px;
}
.player__page__info figure{
    width: 300px;
    height: 300px;
    position: relative;
    border: 3px solid white;
    background: white;
}
.player__page__info figure img{
    width: 100%;
    height: 100%;
}


@media only screen and (max-width: 1050px){
    .players__list__player{
        width: calc(100% / 3 - 8px);
        margin-right: 10px;
    }
    .players__list{
        padding-bottom: 100px;
    }
   
    
    .players__list__player:nth-child(3n){
        margin-right: 0px;
    }
    .players__list__player:nth-child(4n){
        margin-right: 10px;
    }
}

@media only screen and (max-width: 730px){
    .players__list{
        padding-left: 10px;
        padding-right: 10px;
    }
    .players__list__player{
        height: auto;
